$default: #000;
$default-transparent: rgba(0, 0, 0, 0.1);

.lds-ring {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;

    &_center {
        transform: translate(-50%,-50%);
        top: 50%;
        left: 50%;
        position: absolute;
        z-index: 999;
    }
    & div {
        box-sizing: border-box;
        display: block;
        position: absolute;
        width: 64px;
        height: 64px;
        margin: 8px;
        border: 8px solid $default-transparent;
        border-radius: 50%;
        animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
        border-color: $default-transparent transparent transparent transparent;
    }

    & div:nth-child(1) {
        animation-delay: -0.45s;    
    }

    & div:nth-child(2) {
        animation-delay: -0.3s;
    }

    & div:nth-child(3) {
        animation-delay: -0.15s;
    }
}
  
@keyframes lds-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
}
